import React from "react";
import Widget from "./Widget";
import styled from "styled-components";

const ChatPanel = styled.div`
  padding-bottom: 20%;
  padding-left: 5%;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
`;

const WriteMessage = styled.input`
  border: 0.5px solid white;
  padding: 0.5rem;
  background-color: black;
  border-radius: 0.3rem;
  color: white;
  width: 100%;
`;

const Icon = styled.i`
  margin: 0px 5px;
`;

const WriteContainer = styled.div`
  background-color: white;
  display: flex;
  align-items: center;
  border-radius: 0.3rem;
  width: 100%;
`;

const MessageContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 70%;
  margin-bottom: 1%;
  overflow: scroll;
  -ms-overflow-style: none;
  &::-webkit-scrollbar { 
    display: none !important;
  }
`;

const Message = styled.div`
  display: flex;
  justify-content: ${props => (props.mine ? "flex-end" : "flex-start")};

  & + & {
    margin-top: 5px;
  }
`;

const MHeader = styled.div`
  display: flex;
  align-items: center;
`;

const NickName = styled.div`
  font-size: 0.8rem;
  color: #696969;
`;

const MTime = styled.div`
  font-size: 0.7rem;
  color: #9e9e9e;
  margin-left: 10px;
`;

const MContents = styled.div`
  font-family: "Arial";
  font-size: 0.9rem;
  word-break:break-all;
`;

const MWrapper = styled.div`
  display: inline-block;
  background-color: ${props => props.mine ? "#ffeb3b": "white"};
  border-radius: 0.3rem;
  padding: 5px 10px;
`;

const sample = [
  { nickName: "SON.H.M", time: "PM 07:57", contents: "Hi, everyone" },
  {
    nickName: "Park Ji Sung",
    time: "PM 07:59",
    contents: "It's really good music",
    mine: true
  },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "Hi, everyone" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoaasdfasdfasdfasdfasdfasdfasdfasdfasdfasdfasdfasdvasdvasdfasdfwefasdvsd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
  { nickName: "SON.H.M", time: "PM 07:57", contents: "awpeoinvansdoijapowiejfojoasd!" },
];

const ChatWidget = () => {
  return (
    <Widget type="chat" width="40" height="100">
      <ChatPanel>
        <MessageContainer>
          {sample.map((item, i) => (
            <Message key={i} mine={item.mine}>
              <MWrapper mine={item.mine}>
                <MHeader>
                  <NickName>{item.nickName}</NickName>
                  <MTime>{item.time}</MTime>
                </MHeader>
                <MContents>{item.contents}</MContents>
              </MWrapper>
            </Message>
          ))}
        </MessageContainer>
        <WriteContainer>
          <Icon className="material-icons">account_circle</Icon>
          <WriteMessage placeholder="본부장"></WriteMessage>
        </WriteContainer>
      </ChatPanel>
    </Widget>
  );
};

export default ChatWidget;
